/**
 * Page Layout with consistent prop naming and navigation support
 * @module @voilajsx/uikit
 * @file src/components/layouts/page.tsx
 */

import * as React from 'react';
import { forwardRef, createContext, useContext } from 'react';
import { cva } from 'class-variance-authority';
import { cn } from '@/lib/utils';
import { Header, HeaderLogo, HeaderNav } from '@/components/sections/header';
import { Footer } from '@/components/sections/footer';
import type { NavigationItem, Size } from '@/types';

/**
 * Page context for sharing configuration
 */
const PageContext = createContext<{
  variant: 'default' | 'muted' | 'primary' | 'black';
  size: Size;
}>({
  variant: 'default',
  size: 'xl',
});

/**
 * Page layout variants
 */
const pageVariants = cva(
  'min-h-screen flex flex-col',
  {
    variants: {
      variant: {
        default: 'bg-background text-foreground',
        muted: 'bg-muted/20 text-foreground',
        primary: 'bg-primary/5 text-foreground',
        black: 'bg-zinc-900 text-zinc-100'
      }
    },
    defaultVariants: {
      variant: 'default'
    }
  }
);

/**
 * Page content variants
 */
const pageContentVariants = cva(
  'flex-1 w-full'
);

/**
 * Page content inner variants
 */
const pageContentInnerVariants = cva(
  'mx-auto',
  {
    variants: {
      size: {
        sm: 'max-w-2xl px-4 py-6',
        md: 'max-w-4xl px-4 sm:px-6 py-8',
        lg: 'max-w-6xl px-4 sm:px-6 lg:px-8 py-8',
        xl: 'max-w-7xl px-4 sm:px-6 lg:px-8 py-8',
        full: 'max-w-full px-4 sm:px-6 lg:px-8 py-8',
      },
    },
    defaultVariants: {
      size: 'xl',
    },
  }
);

/**
 * Page Layout props
 */
export interface PageLayoutProps {
  /** Page style variant */
  variant?: 'default' | 'muted' | 'primary' | 'black';
  /** Page size (width and spacing) */
  size?: Size;
  /** Whether header should be sticky */
  sticky?: boolean;
  /** Navigation items for header */
  navigation?: NavigationItem[];
  /** Current path for active states */
  currentPath?: string;
  /** Navigation handler */
  onNavigate?: (path: string, item: NavigationItem) => void;
  /** Page title */
  title?: string;
  /** Logo component */
  logo?: React.ReactNode;
  /** Header actions (buttons, theme toggle, etc.) */
  headerActions?: React.ReactNode;
  /** Footer navigation items */
  footerNavigation?: NavigationItem[];
  /** Footer content (if not using footerNavigation) */
  footerContent?: React.ReactNode;
  /** Copyright text for footer */
  copyright?: React.ReactNode;
  /** Additional CSS classes */
  className?: string;
  /** Page content */
  children: React.ReactNode;
}

/**
 * Main PageLayout component with auto-rendering navigation
 */
const PageLayoutComponent = forwardRef<HTMLDivElement, PageLayoutProps>(({
  className,
  variant = 'default',
  size = 'xl',
  sticky = true,
  navigation = [],
  currentPath = '',
  onNavigate,
  title,
  logo,
  headerActions,
  footerNavigation = [],
  footerContent,
  copyright,
  children,
}, ref) => {
  
  // Use navigation or footerNavigation for footer
  const finalFooterNavigation = footerNavigation.length > 0 ? footerNavigation : navigation;

  return (
    <PageContext.Provider value={{ variant, size }}>
      <div
        ref={ref}
        className={cn(pageVariants({ variant }), className)}
      >
        {/* Header */}
        <Header 
          variant={variant}
          size={size}
          sticky={sticky}
        >
          {/* Logo */}
          <HeaderLogo>
            {logo || (title && (
              <span className="text-xl font-bold">{title}</span>
            ))}
          </HeaderLogo>

          {/* Navigation - FIXED: Changed from items to navigation */}
          {navigation.length > 0 && (
            <HeaderNav
              navigation={navigation}
              currentPath={currentPath}
              onNavigate={onNavigate}
            />
          )}

          {/* Header Actions */}
          {headerActions && (
            <div className="ml-auto flex items-center gap-2">
              {headerActions}
            </div>
          )}
        </Header>

        {/* Content */}
        <PageContent size={size}>
          {children}
        </PageContent>

        {/* Footer */}
        <Footer 
          variant={variant}
          size={size}
        >
          {/* Footer Navigation */}
          {finalFooterNavigation.length > 0 && (
            <div className="flex flex-wrap justify-center gap-6 py-4">
              {finalFooterNavigation.map((item) => (
                <button
                  key={item.key}
                  onClick={() => {
                    if (item.path && onNavigate) {
                      onNavigate(item.path, item);
                    } else if (item.onClick) {
                      item.onClick();
                    }
                  }}
                  className="text-sm text-muted-foreground hover:text-foreground transition-colors"
                >
                  {item.label}
                </button>
              ))}
            </div>
          )}

          {/* Custom Footer Content */}
          {footerContent}

          {/* Copyright */}
          {copyright && (
            <div className="text-center py-4 border-t border-border">
              <p className="text-sm text-muted-foreground">
                {copyright}
              </p>
            </div>
          )}
        </Footer>
      </div>
    </PageContext.Provider>
  );
});

PageLayoutComponent.displayName = 'PageLayout';

/**
 * Page Header props
 */
export interface PageHeaderProps {
  /** Header style variant */
  variant?: 'default' | 'muted' | 'primary' | 'black';
  /** Header size (inherits from PageLayout if not specified) */
  size?: Size;
  /** Whether header should be sticky */
  sticky?: boolean;
  /** Navigation items */
  navigation?: NavigationItem[];
  /** Current path for active states */
  currentPath?: string;
  /** Navigation handler */
  onNavigate?: (path: string, item: NavigationItem) => void;
  /** Additional CSS classes */
  className?: string;
  /** Header content */
  children: React.ReactNode;
}

/**
 * Page Header component
 */
const PageHeader = forwardRef<HTMLElement, PageHeaderProps>(({
  variant,
  size,
  sticky = true,
  navigation = [],
  currentPath = '',
  onNavigate,
  className,
  children,
}, ref) => {
  const { variant: pageVariant, size: pageSize } = usePage();
  
  return (
    <Header
      variant={variant || pageVariant}
      size={size || pageSize}
      sticky={sticky}
      className={className}
    >
      {children}
      {/* FIXED: Changed from items to navigation */}
      {navigation.length > 0 && (
        <HeaderNav
          navigation={navigation}
          currentPath={currentPath}
          onNavigate={onNavigate}
        />
      )}
    </Header>
  );
});

PageHeader.displayName = 'PageHeader';

/**
 * Page Content props
 */
export interface PageContentProps {
  /** Content size (inherits from PageLayout if not specified) */
  size?: Size;
  /** Additional CSS classes */
  className?: string;
  /** Content */
  children: React.ReactNode;
}

/**
 * Page Content component - Main content area
 */
const PageContent = forwardRef<HTMLElement, PageContentProps>(({ 
  size,
  className, 
  children,
}, ref) => {
  const { size: pageSize } = usePage();
  const contentSize = size || pageSize;
  
  return (
    <main 
      ref={ref} 
      className={cn(pageContentVariants(), className)}
    >
      <div className={cn(pageContentInnerVariants({ size: contentSize }))}>
        {children}
      </div>
    </main>
  );
});

PageContent.displayName = 'PageContent';

/**
 * Page Footer props
 */
export interface PageFooterProps {
  /** Footer style variant */
  variant?: 'default' | 'muted' | 'primary' | 'black';
  /** Footer size (inherits from PageLayout if not specified) */
  size?: Size;
  /** Footer navigation items */
  navigation?: NavigationItem[];
  /** Current path for active states */
  currentPath?: string;
  /** Navigation handler */
  onNavigate?: (path: string, item: NavigationItem) => void;
  /** Additional CSS classes */
  className?: string;
  /** Footer content */
  children: React.ReactNode;
}

/**
 * Page Footer component
 */
const PageFooter = forwardRef<HTMLElement, PageFooterProps>(({ 
  variant,
  size,
  navigation = [],
  currentPath = '',
  onNavigate,
  className,
  children,
}, ref) => {
  const { variant: pageVariant, size: pageSize } = usePage();
  
  return (
    <Footer 
      variant={variant || pageVariant}
      size={size || pageSize}
      className={className}
    >
      {/* Navigation */}
      {navigation.length > 0 && (
        <div className="flex flex-wrap justify-center gap-6 py-4">
          {navigation.map((item) => (
            <button
              key={item.key}
              onClick={() => {
                if (item.path && onNavigate) {
                  onNavigate(item.path, item);
                } else if (item.onClick) {
                  item.onClick();
                }
              }}
              className="text-sm text-muted-foreground hover:text-foreground transition-colors"
            >
              {item.label}
            </button>
          ))}
        </div>
      )}
      {children}
    </Footer>
  );
});

PageFooter.displayName = 'PageFooter';

/**
 * Hook to access page configuration
 */
const usePage = () => {
  const context = useContext(PageContext);
  if (!context) {
    throw new Error('usePage must be used within a PageLayout component');
  }
  return context;
};

/**
 * PageLayout with compound components
 */
const PageLayout = Object.assign(PageLayoutComponent, {
  Header: PageHeader,
  Content: PageContent,
  Footer: PageFooter,
  Logo: HeaderLogo,
  Nav: HeaderNav,
});

/**
 * Export PageLayout with compound components and individual components
 */
export { 
  PageLayout,
  PageHeader,
  PageContent,
  PageFooter,
  usePage
};